



























































    import {mapState, mapMutations} from 'vuex'
    import {getAddress, getUser, postAddAddress} from '../../util/service/getData.js'
    // import alertTip from '../../components/alertTip.vue'

    export default {
      data(){
            return{
                name: null, //姓名
                sex: 1, //性别
                phone: null, //电话
                address_detail: null, //详细地址
                tag: '', //备注
                tag_type: 1, //备注类型
                phone_bk: false, //是否选择备注电话
                anntherPhoneNumber: '', //备注电话
                showAlert: false, //弹出框
                alertText: null, //弹出框信息
                poi_type: 0, //地址类型
            }
        },
        created(){

        },
        computed: {
            ...mapState([
                'searchAddress', 'geohash', 'userInfo',
            ]),
        },
        methods: {
            ...mapMutations([
                'CONFIRM_ADDRESS'
            ]),
            //选择性别
            chooseSex(sex){
                this.sex = sex;
            },
			searchAdd(){
				uni.navigateTo({
					url:"searchAddress"
				})
			},
            //保存地址信息
            async addAddress(){
                if (!(this.userInfo && this.userInfo.user_id)) {
                    this.showAlert = true;
                    this.alertText = '请登录'
                }else if(!this.name){
                    this.showAlert = true;
                    this.alertText = '请输入姓名'
                }else if(!this.phone){
                    this.showAlert = true;
                    this.alertText = '请输入电话号码'
                }else if(!this.searchAddress){
                    this.showAlert = true;
                    this.alertText = '请选择地址'
                }else if(!this.address_detail){
                    this.showAlert = true;
                    this.alertText = '请输入详细地址'
                }
                if (this.tag == '家') {
                    this.tag_type = 2;
                }else if(this.tag == '学校'){
                    this.tag_type = 3;
                }else if(this.tag == '公司'){
                    this.tag_type = 4;
                }
                let res = await postAddAddress(this.userInfo.user_id, this.searchAddress.name, this.address_detail, this.geohash, this.name, this.phone, this.anntherPhoneNumber, 0, this.sex, this.tag, this.tag_type);
                //保存成功返沪上一页，否则弹出提示框
                if (res.message) {
                    this.showAlert = true;
                    this.alertText = res.message;
                }else {
                    this.CONFIRM_ADDRESS(1);
                    this.$router.go(-1);
                }
            },
        }
    }
